      /* height: 50vh; */
      width: 100%;
      margin: 5px;
    }
      .card--text {
        justify-items: center;
      }
    
    
    .canal-youtube--text {
      margin-top: 15px;
      max-height: 50px;
    }
    .canal-youtube {
      display: grid;
      grid-template-columns: 60px 220px;
      max-height: 50px;
      align-self: top;
    }

