.faixa {
  width: 100%;
  height: 100vh;
  position: relative;
  background: url(/ccbpigeons/static/media/Overview_lofts.60382c4f.jpg);
  background-attachment: scroll;
  background-size: cover;
}
  .pigeon--pic{
    width: 100%;
    height: 100%;
  }
  .title--over--pic {
    font-family: 'Poppins', 'Roboto Condensed', 'Acme', 'Changa One';
    font-size: 65px;
    color: white;
    position: absolute;
    top: 200px;
    left: 250px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

  }
  .subtitle {
    font-family: 'Poppins', 'Acme', 'Changa One';
    font-size: 20px;
    color: white;
    position: absolute;
    top: 265px;
    left: 230px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

  }
  .arrow {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 310px;
    left: 210px;
    border-right: 4px solid white;
    border-bottom: 4px solid white;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation: animate 1.5s infinite;
            animation: animate 1.5s infinite;
  }

  .arrow:hover {
    cursor: pointer;
  }


