.menu {
  display: flex;
  margin: 30px 0 0 0;
  top: -30px;
  position: absolute;
  align-self: center;
  /* left: 50%;
  right: 50%; */
  /* overflow: hidden; */
  width: 100%;
}
.menu.sticky {
  position: fixed;
  background-color: rgb(9, 8, 24);
  opacity: 0.9;
  /* overflow: auto; */
  -webkit-transition: 1s;
  transition: 1s;
  z-index: 10;
}
  .hamburger {
    display: none;
  }
  .cross {
    display: none;
  }
  /* .hamburger.is-active {
    display: none;
  } */
  .menu--dropdown {
    /* justify-content: space-evenly; */
    justify-content: flex-start;
    display: grid;
    grid-template-columns: repeat(6, auto);
    /* position: sticky;
    position: -webkit-sticky; */
    top: -1px;
    /* background-color: #fff; */
    z-index: 9999;
    -webkit-transition: height 2s;
    transition: height 2s;
    width: 100%;
  }

@media only screen and (max-width: 420px) {
.menu {
  /* margin: 10px 15px 0 10px; */
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.menu.sticky {
  display: none;
}
  .hamburger {
    display: flex;
    justify-content: flex-end;
    margin: 8px 25px 0 0;
    cursor: pointer;
  }
  .hamburger.open{
    display: none
  }
  .cross {
    display: flex;
    position: absolute;
    justify-content: flex-end;
    margin: 25px 32px 0 0;
    cursor: pointer;
  }
  .cross.closed{
    display: none
  }
  
    .menu--dropdown {
      grid-template-columns: 100%;
      grid-template-rows: repeat(5, auto);
      padding: 20px;
      -webkit-transition: right ease-in-out 0.35s;
      transition: right ease-in-out 0.35s;
      z-index: 10;
      top: 0;
      position: relative;

    }

    .menu--dropdown.open {
      height: 100%;
      width: 100%;
      padding: 0;
      padding-top: 10px;
      visibility: visible;
      display: block;
      background-color: #f9f9f9;
      color: black;
      z-index: 1;
      -webkit-transition-timing-function: linear;
              transition-timing-function: linear;
    }
    .menu--dropdown.closed {
      /* height: 0px; */
      display: none;  
      -webkit-transition-timing-function: linear;  
              transition-timing-function: linear;
    }
}

